import styled from "styled-components";
import { fetchCoinTickers } from "../api";
import { useQuery } from "react-query";

interface PriceData {
  id: string;
  name: string;
  symbol: string;
  rank: number;
  circulating_supply: number;
  total_supply: number;
  max_supply: number;
  beta_value: number;
  first_data_at: string;
  last_updated: string;
  quotes: {
    USD: {
      ath_date: string;
      ath_price: number;
      market_cap: number;
      market_cap_change_24h: number;
      percent_change_1h: number;
      percent_change_1y: number;
      percent_change_6h: number;
      percent_change_7d: number;
      percent_change_12h: number;
      percent_change_15m: number;
      percent_change_24h: number;
      percent_change_30d: number;
      percent_change_30m: number;
      percent_from_price_ath: number;
      price: number;
      volume_24h: number;
      volume_24h_change_24h: number;
    };
  };
}

interface Test {
  coinId: string;
}

const CoinPrice = styled.div`
  width: 400px;
  height: 100px;
  background-color: ${(props) => props.theme.textColor};
  color: ${(props) => props.theme.bgColor};
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  border-radius: 10px;
  opacity: 0.7;
  font-weight: bold;
`;
const CoinMaxPrice = styled.div`
  width: 400px;
  height: 100px;
  background-color: ${(props) => props.theme.textColor};
  color: ${(props) => props.theme.bgColor};
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  border-radius: 10px;
  opacity: 0.7;
  margin-top: 10px;
  font-weight: bold;
`;
const CoinChangePrice = styled.div`
  width: 400px;
  height: 100px;
  background-color: ${(props) => props.theme.textColor};
  color: ${(props) => props.theme.bgColor};
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  border-radius: 10px;
  opacity: 0.7;
  margin-top: 10px;
  font-weight: bold;
`;
const Price = ({ coinId }: Test) => {
  const { isLoading: tickersLoading, data: tickersData } = useQuery<PriceData>(
    ["tickers", coinId],
    () => fetchCoinTickers(coinId)
    // {
    //   refetchInterval: 5000,
    // }
  );

  return (
    <>
      <CoinPrice>
          : ${tickersData?.quotes.USD.price.toFixed(3)}
      </CoinPrice>
      <CoinMaxPrice>
        24   : ${tickersData?.quotes.USD.volume_24h.toFixed(3)}
      </CoinMaxPrice>
      <CoinChangePrice>
        24   : $
        {tickersData?.quotes.USD.volume_24h_change_24h.toFixed(3)}
      </CoinChangePrice>
    </>
  );
};

export default Price;
